/**
 * Internal component that exposes embedded wallet availability
 *
 * This component must be rendered inside CedrosLoginProvider since
 * it uses hooks that depend on the login context.
 *
 * @internal
 */

import { useEffect } from 'react';
import { useCedrosLogin } from './useCedrosLogin';
import { useWallet } from '../hooks/useWallet';
import { setEmbeddedWalletGlobal, clearEmbeddedWalletGlobal } from '../utils/embeddedWallet';

/**
 * Exposes embedded wallet availability via window global when enabled
 *
 * Only exposes info when:
 * 1. config.wallet.exposeAvailability is true
 * 2. User is logged in
 * 3. Wallet status is known
 *
 * @internal
 */
export function EmbeddedWalletExposure(): null {
  const { config, user } = useCedrosLogin();
  const { status, solanaPubkey, hasExternalWallet } = useWallet();

  const exposeAvailability = config.wallet?.exposeAvailability ?? false;
  const exposePublicKey = config.wallet?.exposePublicKey ?? false;

  useEffect(() => {
    // Don't expose if disabled or user not logged in
    if (!exposeAvailability || !user) {
      clearEmbeddedWalletGlobal();
      return;
    }

    // Don't expose for external wallet users (they use their own wallet)
    if (hasExternalWallet) {
      clearEmbeddedWalletGlobal();
      return;
    }

    // Wait for wallet status to be determined
    if (status === 'loading') {
      return;
    }

    // Check if SSS wallet is enrolled
    const isEnrolled = status === 'enrolled_locked' || status === 'enrolled_unlocked';

    setEmbeddedWalletGlobal({
      available: isEnrolled,
      publicKey: exposePublicKey && isEnrolled ? solanaPubkey : null,
    });

    // Cleanup on unmount or when dependencies change
    return () => {
      clearEmbeddedWalletGlobal();
    };
  }, [exposeAvailability, exposePublicKey, user, status, solanaPubkey, hasExternalWallet]);

  return null;
}
