/**
 * Main wallet hook - orchestrates wallet status and capabilities
 *
 * This is the primary entry point for wallet functionality.
 * It checks capabilities, fetches wallet status, and provides
 * the current state to child components.
 *
 * Handles two wallet types:
 * - External Solana wallet (user signed in with wallet adapter)
 * - SSS embedded wallet (server-side signing with Shamir shares)
 *
 * Safe to call outside CedrosLoginProvider (returns defaults).
 */

import { useState, useEffect, useCallback, useRef } from 'react';
import { getCryptoCapabilities } from '../crypto';
import { useCedrosLoginOptional } from '../context/useCedrosLogin';
import { useWalletMaterial } from './useWalletMaterial';
import type {
  WalletContextValue,
  WalletStatus,
  CryptoCapabilities,
  ShareAAuthMethod,
} from '../types/wallet';

/** Default return value when outside CedrosLoginProvider */
const DEFAULT_WALLET_VALUE: WalletContextValue = {
  status: 'not_enrolled',
  solanaPubkey: null,
  authMethod: null,
  hasExternalWallet: false,
  isUnlocked: false,
  capabilities: null,
  isSupported: false,
  error: null,
  refresh: async () => {},
  clearError: () => {},
};

/**
 * Main wallet hook
 *
 * Provides wallet status, capabilities, and refresh functionality.
 * Distinguishes between external Solana wallet and SSS embedded wallet.
 *
 * Returns safe defaults when called outside CedrosLoginProvider.
 */
export function useWallet(): WalletContextValue {
  // Check if we're inside a CedrosLoginProvider
  const context = useCedrosLoginOptional();
  const hasProvider = context !== null;

  const [status, setStatus] = useState<WalletStatus>('loading');
  const [solanaPubkey, setSolanaPubkey] = useState<string | null>(null);
  const [authMethod, setAuthMethod] = useState<ShareAAuthMethod | null>(null);
  const [hasExternalWallet, setHasExternalWallet] = useState(false);
  const [isUnlocked, setIsUnlocked] = useState(false);
  const [capabilities, setCapabilities] = useState<CryptoCapabilities | null>(null);
  const [error, setError] = useState<string | null>(null);

  // Always call hooks unconditionally (safe to call outside provider)
  const { getStatus, isLoading } = useWalletMaterial();

  // Track if initial fetch has been attempted to prevent infinite retry on error
  const hasAttemptedFetch = useRef(false);

  // Check crypto capabilities on mount
  useEffect(() => {
    if (!hasProvider) return;

    let cancelled = false;

    const checkCapabilities = async () => {
      try {
        const caps = await getCryptoCapabilities();
        if (cancelled) return;
        setCapabilities(caps);

        if (!caps.allSupported) {
          setStatus('error');
          setError(
            'Your browser or device does not support all required features. ' +
              'Please use a modern browser with a platform authenticator.'
          );
        }
      } catch {
        if (cancelled) return;
        setCapabilities(null);
        setStatus('error');
        setError('Failed to check crypto capabilities');
      }
    };

    checkCapabilities();

    return () => {
      cancelled = true;
    };
  }, [hasProvider]);

  // Fetch wallet status
  const refresh = useCallback(async () => {
    if (!hasProvider || !capabilities?.allSupported) {
      return;
    }

    setStatus('loading');
    setError(null);

    try {
      const walletStatus = await getStatus();

      setSolanaPubkey(walletStatus.solanaPubkey ?? null);
      setAuthMethod(walletStatus.authMethod ?? null);
      setHasExternalWallet(walletStatus.hasExternalWallet);
      setIsUnlocked(walletStatus.unlocked);

      if (walletStatus.hasExternalWallet) {
        // User has external wallet - always "enrolled" from their perspective
        setStatus('enrolled_unlocked');
      } else if (walletStatus.enrolled) {
        // SSS wallet enrolled
        setStatus(walletStatus.unlocked ? 'enrolled_unlocked' : 'enrolled_locked');
      } else {
        setStatus('not_enrolled');
      }
    } catch (err) {
      setStatus('error');
      setError(err instanceof Error ? err.message : 'Failed to fetch wallet status');
    }
  }, [hasProvider, capabilities?.allSupported, getStatus]);

  // Refresh on mount and when capabilities become available
  // Only attempt once to prevent infinite retry loop on API errors
  useEffect(() => {
    if (hasProvider && capabilities?.allSupported && !isLoading && !hasAttemptedFetch.current) {
      hasAttemptedFetch.current = true;
      void refresh();
    }
  }, [hasProvider, capabilities?.allSupported, isLoading, refresh]);

  const clearError = useCallback(() => setError(null), []);

  // Return defaults when outside provider
  if (!hasProvider) {
    return DEFAULT_WALLET_VALUE;
  }

  return {
    status,
    solanaPubkey,
    authMethod,
    hasExternalWallet,
    isUnlocked,
    capabilities,
    isSupported: capabilities?.allSupported ?? false,
    error,
    refresh,
    clearError,
  };
}
