import { useEffect } from 'react';
import type { ThemeMode, ThemeOverrides } from '../types';

export interface UseThemeManagerOptions {
  theme?: ThemeMode;
  themeOverrides?: ThemeOverrides;
}

/**
 * Reference count for the cedros-dark class on document.documentElement.
 * Prevents one provider from removing the class while another still needs it.
 */
let darkModeRefCount = 0;

/** @internal Reset ref count between tests */
export function _resetDarkModeRefCount(): void {
  darkModeRefCount = 0;
}

/**
 * Hook that manages theme application to the document root.
 * Handles light/dark/auto themes and custom CSS variable overrides.
 * Uses reference counting so multiple providers don't conflict on dark mode.
 */
export function useThemeManager({ theme, themeOverrides }: UseThemeManagerOptions): void {
  useEffect(() => {
    if (typeof document === 'undefined' || typeof window === 'undefined') {
      return;
    }

    const root = document.documentElement;

    // Determine if this instance wants dark mode
    let wantsDark = false;
    if (theme === 'dark') {
      wantsDark = true;
    } else if (theme === 'light') {
      wantsDark = false;
    } else {
      // Auto: check system preference
      wantsDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    }

    // Reference-counted dark mode class management
    let addedDarkRef = false;
    if (wantsDark) {
      darkModeRefCount++;
      addedDarkRef = true;
      root.classList.add('cedros-dark');
    } else if (darkModeRefCount === 0) {
      // Only remove if no other instance holds a dark ref
      root.classList.remove('cedros-dark');
    }

    // Track applied overrides for cleanup (store previous values)
    const appliedOverrides: Map<string, string> = new Map();

    // Apply theme overrides
    if (themeOverrides) {
      Object.entries(themeOverrides).forEach(([key, value]) => {
        if (value) {
          // Store previous value for restoration on cleanup
          const previousValue = root.style.getPropertyValue(key);
          appliedOverrides.set(key, previousValue);
          root.style.setProperty(key, value);
        }
      });
    }

    // Cleanup: restore previous state on unmount or when theme changes
    return () => {
      if (addedDarkRef) {
        darkModeRefCount--;
        if (darkModeRefCount === 0) {
          root.classList.remove('cedros-dark');
        }
      }

      // Restore previous CSS variable values
      appliedOverrides.forEach((previousValue, key) => {
        if (previousValue) {
          root.style.setProperty(key, previousValue);
        } else {
          root.style.removeProperty(key);
        }
      });
    };
  }, [theme, themeOverrides]);
}
