import { useState, useEffect, useCallback, useRef, useMemo } from 'react';
import type { AuthUser, AuthState, TokenPair, SessionConfig, AuthCallbacks } from '../types';
import { TokenManager } from '../utils/tokenManager';
import { TabSync } from '../utils/tabSync';
import { getCsrfToken } from '../utils/csrf';

export interface UseAuthSessionOptions {
  serverUrl: string;
  session?: SessionConfig;
  callbacks?: AuthCallbacks;
  requestTimeoutMs?: number;
}

export interface UseAuthSessionReturn {
  user: AuthUser | null;
  authState: AuthState;
  handleLoginSuccess: (user: AuthUser, tokens?: TokenPair) => void;
  logout: () => Promise<void>;
  refreshUser: () => Promise<void>;
  getAccessToken: () => string | null;
}

/**
 * UI-5 FIX: Type guard to validate user response from server.
 * Ensures data.user exists and has required fields before setting state.
 */
function isValidUserResponse(data: unknown): data is { user: AuthUser } {
  if (typeof data !== 'object' || data === null) return false;
  const obj = data as Record<string, unknown>;
  if (typeof obj.user !== 'object' || obj.user === null) return false;
  const user = obj.user as Record<string, unknown>;
  // Validate required fields exist
  return typeof user.id === 'string' && user.id.length > 0;
}

/**
 * UI-RESP: Type guard to validate token response from refresh endpoint.
 * Ensures tokens object has required fields before storing.
 */
function isValidTokenResponse(tokens: unknown): tokens is TokenPair {
  if (typeof tokens !== 'object' || tokens === null) return false;
  const obj = tokens as Record<string, unknown>;
  return (
    typeof obj.accessToken === 'string' &&
    obj.accessToken.length > 0 &&
    typeof obj.refreshToken === 'string' &&
    obj.refreshToken.length > 0 &&
    typeof obj.expiresIn === 'number' &&
    obj.expiresIn > 0
  );
}

/**
 * Hook that manages authentication session state, token refresh, and tab sync.
 */
export function useAuthSession({
  serverUrl,
  session,
  callbacks,
  requestTimeoutMs,
}: UseAuthSessionOptions): UseAuthSessionReturn {
  const [user, setUser] = useState<AuthUser | null>(null);
  const [authState, setAuthState] = useState<AuthState>('idle');

  const tokenManagerRef = useRef<TokenManager | null>(null);
  const tabSyncRef = useRef<TabSync | null>(null);
  const callbacksRef = useRef(callbacks);
  const isMountedRef = useRef(true);
  const refreshInFlightRef = useRef<Promise<void> | null>(null);
  // S-18: Stable callback refs — updated after definitions, used by init effect
  const refreshTokensRef = useRef<() => Promise<void>>(() => Promise.resolve());
  const handleSessionExpiredRef = useRef<() => void>(() => {});

  // Keep callbacks ref in sync
  useEffect(() => {
    callbacksRef.current = callbacks;
  }, [callbacks]);

  useEffect(() => {
    isMountedRef.current = true;
    return () => {
      isMountedRef.current = false;
    };
  }, []);

  const setUserIfMounted = useCallback((nextUser: AuthUser | null) => {
    if (isMountedRef.current) {
      setUser(nextUser);
    }
  }, []);

  const setAuthStateIfMounted = useCallback((nextState: AuthState) => {
    if (isMountedRef.current) {
      setAuthState(nextState);
    }
  }, []);

  // Memoize session config to prevent unnecessary effect reruns
  const sessionConfig = useMemo(
    () => ({
      storage: session?.storage ?? 'cookie',
      autoRefresh: session?.autoRefresh ?? true,
      syncTabs: session?.syncTabs ?? true,
      persistKey: session?.persistKey,
      allowWebStorage: session?.allowWebStorage ?? false,
    }),
    [
      session?.storage,
      session?.autoRefresh,
      session?.syncTabs,
      session?.persistKey,
      session?.allowWebStorage,
    ]
  );

  // Initialize managers on mount
  // S-18: Register callbacks via stable ref wrappers in the same effect that
  // creates the manager, eliminating the window where refresh is unregistered.
  useEffect(() => {
    const manager = new TokenManager(sessionConfig.storage, sessionConfig.persistKey, {
      allowWebStorage: sessionConfig.allowWebStorage,
    });
    tokenManagerRef.current = manager;

    if (sessionConfig.autoRefresh) {
      manager.setRefreshCallback(() => refreshTokensRef.current());
    }
    manager.setSessionExpiredCallback(() => handleSessionExpiredRef.current());

    if (sessionConfig.syncTabs) {
      tabSyncRef.current = new TabSync();
    }

    return () => {
      manager.destroy();
      tokenManagerRef.current = null;
      tabSyncRef.current?.close();
    };
  }, [
    sessionConfig.storage,
    sessionConfig.syncTabs,
    sessionConfig.persistKey,
    sessionConfig.allowWebStorage,
    sessionConfig.autoRefresh,
  ]);

  // Token refresh function
  const refreshTokens = useCallback(async (): Promise<void> => {
    // UI-RACE: Check for in-flight request first
    if (refreshInFlightRef.current) {
      return refreshInFlightRef.current;
    }

    const refreshToken = tokenManagerRef.current?.getRefreshToken();
    const hasRefreshToken = Boolean(refreshToken);
    const csrfToken = getCsrfToken();
    const headers: Record<string, string> = {};

    if (hasRefreshToken) {
      headers['Content-Type'] = 'application/json';
    }
    if (csrfToken) {
      headers['X-CSRF-Token'] = csrfToken;
    }

    // UI-02 FIX: Set the sentinel synchronously before the async IIFE is created.
    // This closes the microtask-window race where two callers both pass the
    // in-flight check before either has assigned refreshInFlightRef.current.
    // We use a deferred-resolve pattern so the ref is set before any await.
    let resolveDeferred!: () => void;
    let rejectDeferred!: (e: unknown) => void;
    const deferredPromise = new Promise<void>((res, rej) => {
      resolveDeferred = res;
      rejectDeferred = rej;
    });
    refreshInFlightRef.current = deferredPromise;

    const refreshPromise = (async () => {
      const controller = new AbortController();
      const timeoutMs = requestTimeoutMs ?? 10_000;
      const timeoutId = window.setTimeout(() => controller.abort(), timeoutMs);

      try {
        const response = await fetch(`${serverUrl}/refresh`, {
          method: 'POST',
          headers: Object.keys(headers).length > 0 ? headers : undefined,
          credentials: 'include',
          body: hasRefreshToken ? JSON.stringify({ refreshToken }) : undefined,
          signal: controller.signal,
        });

        if (!response.ok) {
          throw new Error('Token refresh failed');
        }

        const data = await response.json();
        // UI-RESP: Validate token structure before storing
        if (data.tokens) {
          if (!isValidTokenResponse(data.tokens)) {
            throw new Error('Invalid token response structure');
          }
          tokenManagerRef.current?.setTokens(data.tokens);
        } else if (sessionConfig.storage !== 'cookie') {
          throw new Error('Token refresh failed');
        }
        tabSyncRef.current?.broadcastRefresh();
        resolveDeferred();
      } catch (e) {
        rejectDeferred(e);
        throw e;
      } finally {
        window.clearTimeout(timeoutId);
      }
    })();

    // Suppress unhandled-rejection on the inner IIFE (errors are forwarded via
    // rejectDeferred so deferredPromise is the authoritative rejection source).
    refreshPromise.catch(() => {});

    try {
      await deferredPromise;
    } finally {
      refreshInFlightRef.current = null;
    }
  }, [serverUrl, sessionConfig.storage, requestTimeoutMs]);

  const getAuthHeaders = useCallback((): Record<string, string> | undefined => {
    if (sessionConfig.storage === 'cookie') return undefined;
    const accessToken = tokenManagerRef.current?.getAccessToken();
    if (!accessToken) return undefined;
    return { Authorization: `Bearer ${accessToken}` };
  }, [sessionConfig.storage]);

  // Handle session expiry
  const handleSessionExpired = useCallback(() => {
    tokenManagerRef.current?.clear();
    setUserIfMounted(null);
    setAuthStateIfMounted('unauthenticated');
    callbacksRef.current?.onSessionExpired?.();
  }, [setAuthStateIfMounted, setUserIfMounted]);

  // S-18: Keep refs in sync with latest callback versions
  refreshTokensRef.current = refreshTokens;
  handleSessionExpiredRef.current = handleSessionExpired;

  // S-17: Helper to build fetch options with timeout + CSRF
  const fetchWithTimeoutAndCsrf = useCallback(
    (url: string): { promise: Promise<Response>; cleanup: () => void } => {
      const controller = new AbortController();
      const timeoutMs = requestTimeoutMs ?? 10_000;
      const timeoutId = window.setTimeout(() => controller.abort(), timeoutMs);
      const headers: Record<string, string> = {};
      const authHeaders = getAuthHeaders();
      if (authHeaders) Object.assign(headers, authHeaders);
      const csrfToken = getCsrfToken();
      if (csrfToken) headers['X-CSRF-Token'] = csrfToken;

      return {
        promise: fetch(url, {
          credentials: 'include',
          headers: Object.keys(headers).length > 0 ? headers : undefined,
          signal: controller.signal,
        }),
        cleanup: () => window.clearTimeout(timeoutId),
      };
    },
    [getAuthHeaders, requestTimeoutMs]
  );

  // Refresh user data from server
  const refreshUser = useCallback(async () => {
    const req = fetchWithTimeoutAndCsrf(`${serverUrl}/user`);
    try {
      const response = await req.promise;

      if (response.ok) {
        const data = await response.json();
        if (isValidUserResponse(data)) {
          setUserIfMounted(data.user);
          setAuthStateIfMounted('authenticated');
          return;
        }
      }

      if (response.status === 401 && sessionConfig.autoRefresh) {
        try {
          await refreshTokens();
        } catch {
          handleSessionExpired();
          return;
        }
        const retry = fetchWithTimeoutAndCsrf(`${serverUrl}/user`);
        try {
          const retryResponse = await retry.promise;
          if (retryResponse.ok) {
            const data = await retryResponse.json();
            if (isValidUserResponse(data)) {
              setUserIfMounted(data.user);
              setAuthStateIfMounted('authenticated');
              return;
            }
          }
        } finally {
          retry.cleanup();
        }
      }

      setUserIfMounted(null);
      setAuthStateIfMounted('unauthenticated');
    } catch {
      // On network/unexpected error, clear user and mark unauthenticated
      setUserIfMounted(null);
      setAuthStateIfMounted('unauthenticated');
    } finally {
      req.cleanup();
    }
  }, [
    serverUrl,
    sessionConfig.autoRefresh,
    refreshTokens,
    fetchWithTimeoutAndCsrf,
    handleSessionExpired,
    setAuthStateIfMounted,
    setUserIfMounted,
  ]);

  // Setup tab sync callbacks
  useEffect(() => {
    if (!tabSyncRef.current || !sessionConfig.syncTabs) return;

    tabSyncRef.current.setCallback((event) => {
      switch (event.type) {
        case 'login':
          setUserIfMounted(event.user);
          setAuthStateIfMounted('authenticated');
          break;
        case 'logout':
          setUserIfMounted(null);
          setAuthStateIfMounted('unauthenticated');
          tokenManagerRef.current?.clear();
          break;
        case 'refresh':
          refreshUser();
          break;
        default: {
          // Exhaustiveness check - TypeScript will error if a new event type is added
          const _exhaustiveCheck: never = event;
          console.warn('[Cedros Login] Unhandled tab sync event:', _exhaustiveCheck);
        }
      }
    });
  }, [sessionConfig.syncTabs, refreshUser, setAuthStateIfMounted, setUserIfMounted]);

  // Check for existing session on mount
  useEffect(() => {
    const controller = new AbortController();
    // UI-TIMEOUT: Add timeout to session check fetch (default 10s, same as refresh)
    const timeoutMs = requestTimeoutMs ?? 10_000;
    const timeoutId = window.setTimeout(() => controller.abort(), timeoutMs);

    const checkSession = async () => {
      setAuthStateIfMounted('loading');
      try {
        const response = await fetch(`${serverUrl}/user`, {
          credentials: 'include',
          headers: getAuthHeaders(),
          signal: controller.signal,
        });

        if (response.ok) {
          const data = await response.json();
          if (isValidUserResponse(data)) {
            setUserIfMounted(data.user);
            setAuthStateIfMounted('authenticated');
            return;
          }
        }

        if (response.status === 401 && sessionConfig.autoRefresh) {
          try {
            await refreshTokens();
          } catch {
            handleSessionExpired();
            return;
          }
          const retryResponse = await fetch(`${serverUrl}/user`, {
            credentials: 'include',
            headers: getAuthHeaders(),
            signal: controller.signal,
          });
          if (retryResponse.ok) {
            const data = await retryResponse.json();
            if (isValidUserResponse(data)) {
              setUserIfMounted(data.user);
              setAuthStateIfMounted('authenticated');
              return;
            }
          }
        }

        setUserIfMounted(null);
        setAuthStateIfMounted('unauthenticated');
      } catch {
        setUserIfMounted(null);
        setAuthStateIfMounted('unauthenticated');
      }
    };

    checkSession();
    return () => {
      window.clearTimeout(timeoutId);
      controller.abort();
    };
  }, [
    serverUrl,
    sessionConfig.autoRefresh,
    refreshTokens,
    getAuthHeaders,
    handleSessionExpired,
    setAuthStateIfMounted,
    setUserIfMounted,
    requestTimeoutMs,
  ]);

  // Handle successful login
  const handleLoginSuccess = useCallback(
    (authUser: AuthUser, tokens?: TokenPair) => {
      // UI-01: Use mounted-safe setters to prevent state updates after unmount
      setUserIfMounted(authUser);
      setAuthStateIfMounted('authenticated');
      if (tokens) {
        tokenManagerRef.current?.setTokens(tokens);
      }
      // M-05: Guard broadcast with mounted check to avoid calling closed TabSync
      if (isMountedRef.current) {
        tabSyncRef.current?.broadcastLogin(authUser);
      }
    },
    [setUserIfMounted, setAuthStateIfMounted]
  );

  // Logout function
  const logout = useCallback(async () => {
    const csrfToken = getCsrfToken();
    const controller = new AbortController();
    const timeoutMs = requestTimeoutMs ?? 10_000;
    const timeoutId = window.setTimeout(() => controller.abort(), timeoutMs);
    try {
      await fetch(`${serverUrl}/logout`, {
        method: 'POST',
        headers: {
          ...(csrfToken ? { 'X-CSRF-Token': csrfToken } : {}),
          ...(getAuthHeaders() ?? {}),
        },
        credentials: 'include',
        signal: controller.signal,
      });
    } catch {
      // Ignore logout errors (including AbortError on timeout)
    } finally {
      window.clearTimeout(timeoutId);
      // UI-01: Use mounted-safe setters to prevent state updates after unmount
      setUserIfMounted(null);
      setAuthStateIfMounted('unauthenticated');
      tokenManagerRef.current?.clear();
      tabSyncRef.current?.broadcastLogout();
      callbacksRef.current?.onLogout?.();
    }
  }, [serverUrl, getAuthHeaders, setUserIfMounted, setAuthStateIfMounted, requestTimeoutMs]);

  // Get access token
  const getAccessToken = useCallback(() => {
    return tokenManagerRef.current?.getAccessToken() ?? null;
  }, []);

  return {
    user,
    authState,
    handleLoginSuccess,
    logout,
    refreshUser,
    getAccessToken,
  };
}
