/**
 * Hook for wallet material API operations
 *
 * Handles communication with the server for wallet enrollment,
 * material retrieval, transaction signing, and credential rotation.
 *
 * Safe to call outside CedrosLoginProvider (returns no-op functions).
 */

import { useState, useCallback, useMemo } from 'react';
import { useCedrosLoginOptional } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import type {
  UseWalletMaterialReturn,
  WalletMaterialResponse,
  WalletEnrollRequest,
  WalletRecoverRequest,
  RotateUserSecretRequest,
  SignTransactionRequest,
  SignTransactionResponse,
  MessageResponse,
  WalletStatusApiResponse,
  UnlockCredential,
  WalletUnlockResponse,
  ShareCRecoveryRequest,
  ShareCRecoveryResponse,
  CreateDerivedWalletRequest,
  DerivedWalletResponse,
  AllWalletsListResponse,
} from '../types/wallet';
import { toCredentialRequest } from '../utils/unlockCredential';

/**
 * Hook for wallet material API operations
 *
 * Signing happens server-side: server stores Share A (encrypted) and Share B
 * (plaintext), combines shares JIT for signing, and wipes immediately after.
 *
 * Safe to call outside CedrosLoginProvider - returns no-op functions that throw.
 */
export function useWalletMaterial(): UseWalletMaterialReturn {
  const context = useCedrosLoginOptional();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const serverUrl = context?.config.serverUrl;
  const requestTimeout = context?.config.requestTimeout;
  const retryAttempts = context?.config.retryAttempts;
  const getAccessToken = context?._internal?.getAccessToken;

  const apiClient = useMemo(() => {
    if (!context) return null;
    return new ApiClient({
      baseUrl: serverUrl!,
      timeoutMs: requestTimeout,
      retryAttempts: retryAttempts,
      getAccessToken,
    });
  }, [context, serverUrl, requestTimeout, retryAttempts, getAccessToken]);

  const getStatus = useCallback(async (): Promise<WalletStatusApiResponse> => {
    if (!apiClient) {
      throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      const data = await apiClient.get<WalletStatusApiResponse>('/wallet/status');
      return data;
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to fetch wallet status');
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const getMaterial = useCallback(async (): Promise<WalletMaterialResponse | null> => {
    if (!apiClient) {
      throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      const data = await apiClient.get<WalletMaterialResponse>('/wallet/material');
      return data;
    } catch (err) {
      // 404 means no wallet enrolled - not an error
      const apiError = handleApiError(err, 'Failed to fetch wallet material');
      if (apiError.code === 'NOT_FOUND') {
        return null;
      }
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const enroll = useCallback(
    async (request: WalletEnrollRequest): Promise<void> => {
      if (!apiClient) {
        throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        await apiClient.post<MessageResponse>('/wallet/enroll', request);
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to enroll wallet');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const recover = useCallback(
    async (request: WalletRecoverRequest): Promise<void> => {
      if (!apiClient) {
        throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        await apiClient.post<MessageResponse>('/wallet/recover', request);
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to recover wallet');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const signTransaction = useCallback(
    async (request: SignTransactionRequest): Promise<SignTransactionResponse> => {
      if (!apiClient) {
        throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const response = await apiClient.post<SignTransactionResponse>('/wallet/sign', request);
        return response;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to sign transaction');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const rotateUserSecret = useCallback(
    async (request: RotateUserSecretRequest): Promise<void> => {
      if (!apiClient) {
        throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        await apiClient.post<MessageResponse>('/wallet/rotate-user-secret', request);
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to rotate user secret');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const unlock = useCallback(
    async (credential: UnlockCredential): Promise<WalletUnlockResponse> => {
      if (!apiClient) {
        throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const response = await apiClient.post<WalletUnlockResponse>(
          '/wallet/unlock',
          toCredentialRequest(credential)
        );
        return response;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to unlock wallet');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const lock = useCallback(async (): Promise<void> => {
    if (!apiClient) {
      throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      await apiClient.post<MessageResponse>('/wallet/lock', {});
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to lock wallet');
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const getShareBForRecovery = useCallback(
    async (request: ShareCRecoveryRequest): Promise<ShareCRecoveryResponse> => {
      if (!apiClient) {
        throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        const response = await apiClient.post<ShareCRecoveryResponse>('/wallet/share-b', request);
        return response;
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to get Share B for recovery');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const createDerivedWallet = useCallback(
    async (request: CreateDerivedWalletRequest): Promise<DerivedWalletResponse> => {
      if (!apiClient) {
        throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        return await apiClient.post<DerivedWalletResponse>('/wallet/derived', request);
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to create derived wallet');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const listAllWallets = useCallback(async (): Promise<AllWalletsListResponse> => {
    if (!apiClient) {
      throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
    }
    setIsLoading(true);
    setError(null);

    try {
      return await apiClient.get<AllWalletsListResponse>('/wallet/derived');
    } catch (err) {
      const apiError = handleApiError(err, 'Failed to list wallets');
      setError(apiError.message);
      throw apiError;
    } finally {
      setIsLoading(false);
    }
  }, [apiClient]);

  const deleteDerivedWallet = useCallback(
    async (walletId: string): Promise<void> => {
      if (!apiClient) {
        throw new Error('useWalletMaterial must be used within a CedrosLoginProvider');
      }
      setIsLoading(true);
      setError(null);

      try {
        await apiClient.delete<MessageResponse>(`/wallet/derived/${walletId}`);
      } catch (err) {
        const apiError = handleApiError(err, 'Failed to delete derived wallet');
        setError(apiError.message);
        throw apiError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient]
  );

  const clearError = useCallback(() => setError(null), []);

  return {
    getStatus,
    getMaterial,
    enroll,
    recover,
    signTransaction,
    rotateUserSecret,
    unlock,
    lock,
    getShareBForRecovery,
    createDerivedWallet,
    listAllWallets,
    deleteDerivedWallet,
    isLoading,
    error,
    clearError,
  };
}
