import type { AuthUser } from '../types';

const CHANNEL_NAME = 'cedros_auth_sync';

type AuthSyncEvent = { type: 'login'; user: AuthUser } | { type: 'logout' } | { type: 'refresh' };

type AuthSyncCallback = (event: AuthSyncEvent) => void;

/**
 * Cross-tab synchronization for auth state using BroadcastChannel
 * UI-6 FIX: Use addEventListener/removeEventListener for proper cleanup.
 */
export class TabSync {
  private channel: BroadcastChannel | null = null;
  private callback: AuthSyncCallback | null = null;
  private boundHandler: ((event: MessageEvent<AuthSyncEvent>) => void) | null = null;

  constructor() {
    if (typeof window !== 'undefined' && 'BroadcastChannel' in window) {
      this.channel = new BroadcastChannel(CHANNEL_NAME);
      // UI-6: Store bound handler reference for proper removal
      this.boundHandler = this.handleMessage.bind(this);
      this.channel.addEventListener('message', this.boundHandler);
    }
  }

  /**
   * Handle incoming sync messages.
   * S-15: Validate message shape to prevent forged auth state from same-origin XSS.
   */
  private handleMessage(event: MessageEvent<AuthSyncEvent>): void {
    const data = event.data;
    if (!data || typeof data !== 'object' || typeof data.type !== 'string') return;
    if (!['login', 'logout', 'refresh'].includes(data.type)) return;
    // Validate user structure for login events to prevent forged auth state
    if (data.type === 'login') {
      const loginData = data as { type: 'login'; user: unknown };
      if (
        typeof loginData.user !== 'object' ||
        loginData.user === null ||
        typeof (loginData.user as Record<string, unknown>).id !== 'string'
      ) {
        return;
      }
    }
    this.callback?.(data);
  }

  /**
   * Set the callback for sync events from other tabs
   */
  setCallback(callback: AuthSyncCallback): void {
    this.callback = callback;
  }

  /**
   * Broadcast login event to other tabs
   */
  broadcastLogin(user: AuthUser): void {
    this.channel?.postMessage({ type: 'login', user });
  }

  /**
   * Broadcast logout event to other tabs
   */
  broadcastLogout(): void {
    this.channel?.postMessage({ type: 'logout' });
  }

  /**
   * Broadcast token refresh event to other tabs
   */
  broadcastRefresh(): void {
    this.channel?.postMessage({ type: 'refresh' });
  }

  /**
   * Close the channel and clean up references
   * UI-6: Use removeEventListener for proper cleanup
   */
  close(): void {
    if (this.channel) {
      if (this.boundHandler) {
        this.channel.removeEventListener('message', this.boundHandler);
        this.boundHandler = null;
      }
      this.channel.close();
      this.channel = null;
    }
    this.callback = null;
  }
}
